:root{
  --bg:white;
  --text:#3a3a83;
  --other:#A81470;
  --body_size: 18px;
  --header_size:54px;
  --body_size_phone: 18px;
  --header_size_phone:32px;
  --portrait_width:40%;
  --portrait_width_phone:95%;
}

.dark-theme{
  --bg: #1b154d;
  --text: #c5c3e6;
  --other:#D097BE;
}

.App {
  text-align: left;
  display: flex;
  flex-direction: column
}

#togglecont{
  color: var(--text);
  margin-bottom: 1.3em
} 

#togglecont:hover{
  text-decoration: underline;
  text-decoration-color: var(--text);
}

.container{
  margin-top: 5em;
  margin-left: 5em;
  margin-right: 5em;
  margin-bottom:5em;
  display: flex;
  z-index: 1;
  overscroll-behavior: contain;
}

.navtext{
  color: var(--text);
  text-align: left;
  font-size: var(--body_size);
  margin-top: 0em;
  margin-left: 5em;
  font-weight: bold;
  position: fixed;
  line-height: 1.3em
  
}

.navtext nav{
  color: var(--text);
}

.navtext ul{
  padding-left: 0px
}

.navtext li{
  list-style-type: none;
  display: block
}

.navtext a{
  color: var(--text);
  text-decoration: none
}

.navtext a:hover{
  text-decoration: underline;
 
}

.text{
  margin-left: 20em;
  margin-right: 1em;
  color: var(--text);
  font-weight:500;
  text-align: left;
  font-size: var(--body_size);
  margin-bottom: auto;
  max-width: 100%;
  line-height:1.3em;
  font-family: Arial, Helvetica, sans-serif
}

#ref{
  text-decoration: var(--text) none;
  /* color: var(--text); */
  color: var(--other);
  font-weight: none;
}

#ref:hover:hover{
  flex: 4;
  text-decoration: underline
}
.contact span{
  padding-right: 10px
}

.welcome{
  font-size: var(--header_size);
  font-weight: 700;
  line-height: 1em;
  text-align: left;
  margin-top:0em;
}
.star-list {
  padding-left: 0em; /* small indent ensures marker displays on mobile */
  margin: 0;
}

.star-list li {
  list-style: none;    /* remove default bullets if you want custom */
  margin: 0.75em 0;    /* vertical spacing between items */
}

/* Remove top margin from first list item */
.star-list li:first-child {
  margin-top: 0;
}

.star-list li::marker {
  content: "âœ¦ ";        /* simple Unicode star */
  color: var(--text);  
  font-size: 1em;       /* avoid scaling too much */
}

#ilocontainer{
  display: flex;
}

#iloa{
  max-width:100%;
  padding-right: 1em;
}

#ilos{
  width: 50%;
  height: auto
}
#decisiontree{
  display: flex;
  width: 100%
}
#dttext{
  flex: 5;
}
#dtContainer{
  flex: 8;
  display: flex;
  flex-direction: column;
}

#dt{
  width: 100%;
  display: block;
}
.iconcontainer {
  display: flex;
  align-items: center; /* Aligns icons and text vertically */
  gap: 8px; /* Adds space between icons */
}

.icons {
  height: 1em; /* Match the text height */
  width: auto; /* Maintains the aspect ratio */
  display: inline-block; /* Ensures icons align with text */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
#portrait{
  width: var(--portrait_width);
  height: auto;

}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@media only screen and (max-width: 900px){
  #ilocontainer{
    display: none;
    visibility: hidden;
  }

  #header{
    text-decoration: underline;
  } 

  .container{
    margin-top: 2em;
    margin-left: 0.5em;
    margin-right: 0.5em;
    margin-bottom: 3em;
    z-index: 1;
  }
  
  .navtext{
    color: var(--text);
    text-align: left;
    font-size: var(--body_size_phone);
    margin-top: 0em;
    margin-right:0;
    font-weight: bold;
    position: fixed;
    visibility: hidden;
    font-family: Arial Narrow, sans-serif
  }

  .text{
    font-size:var(--body_size_phone);
    display:block;
    margin-left: auto;
    margin-right:auto;
    color: var(--text);
    text-align: left;
    max-width: 90%;
    font-family: Arial, Helvetica, sans-serif;
  }
  
  #iloa{
    max-width:100%;
  }
  
  #ilos{
    width: 100%;
    height: auto
  }
  #ref{
    color: var(--other);
  }

  .welcome span{
    font-size: 64px;
  }
  .welcome{
    font-weight: normal;
    line-height: 1em;
    text-align: left;
    margin-top:0em;
    font-family: Arial, Helvetica, sans-serif
  }

  #portrait{
    width: var(--portrait_width_phone);
    height: auto;
    margin-top: 1em;
    margin-bottom:0em;
  }
  .star-list li {
    list-style-position: inside; /* keeps bullets aligned */
    margin: 1em 0;
  }

  .star-list li::marker {
    content: none;           /* disable custom star */
  }

  @media only screen and (min-width: 901px) {
    #nav-toggle, #nav-page {
      display: none !important; /* hide mobile nav on desktop */
    }
  }

}
